.beecost-btn-wrapper {
  position: relative;
}
#beecost-btn {
  position: absolute;
  top: 50%;
  right: 0;
  transform: translate(0, -50%);
  white-space: nowrap;
  max-width: none;
  padding-left: 35px;
  font-weight: bold;
  text-transform: uppercase;
  z-index: 1;
  width: 240px;
}
#beecost-btn img {
  position: absolute;
  left: 15px;
}
#beecost-btn.beecost-tut-showed {
  z-index: 99999;
}
#beecost-chart-wrapper {
  background: #fff;
  padding-top: 1px;
  overflow: unset;
  position: relative;
  z-index: 1;
}
#beecost-chart-wrapper.beecost-hidden {
  height: 0;
}
#beecost-chart-wrapper.beecost-tut-showed {
  z-index: 99999;
}
#beecost-overlay {
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.35);
  width: 100%;
  height: 100%;
  z-index: 99998;
}
.beecost-tut {
  position: absolute;
  border: 2px solid #ff5722;
  z-index: 99999;
  border-radius: 3px;
  background: #fff;
  padding: 15px;
  box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.09);
  text-align: justify;
  line-height: 1.4;
}
.beecost-tut p {
  margin: 0 0 10px;
}
.beecost-tut b {
  color: #ff5722;
  text-transform: uppercase;
}
.beecost-tut a {
  color: gray;
  text-decoration: underline;
  cursor: pointer;
}
.beecost-tut a:hover {
  text-decoration: none;
}
.beecost-tut:before,
.beecost-tut:after {
  position: absolute;
  content: '';
}
.beecost-tut:before {
  border: 10px solid transparent;
  z-index: 2;
}
.beecost-tut:after {
  border: 12px solid transparent;
  z-index: 1;
}
#beecost-btn-tut {
  width: 280px;
  top: 16px;
  right: 255px;
}
#beecost-btn-tut:after {
  border-left-color: #ff5722;
  border-bottom-width: 12px;
  border-right-width: 12px;
  right: -24px;
  top: 6px;
}
#beecost-btn-tut:before {
  border-left-color: #fff;
  border-bottom-width: 10px;
  border-right-width: 10px;
  right: -19px;
  top: 8px;
}
#beecost-chart-tut {
  width: 400px;
  top: -190px;
  left: 20px;
}
#beecost-chart-tut:after {
  border-top-color: #ff5722;
  border-bottom-width: 12px;
  border-left-width: 12px;
  bottom: -24px;
  left: 6px;
}
#beecost-chart-tut:before {
  border-top-color: #fff;
  border-bottom-width: 10px;
  border-left-width: 10px;
  bottom: -19px;
  left: 8px;
}
